
import React from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// @material-ui/icons

// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";

import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import Checkbox from '@material-ui/core/Checkbox';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FlagRoundedIcon from '@material-ui/icons/FlagRounded';

import styles from "assets/jss/material-kit-react/views/catalogue/FilterStyle.jsx";

const useStyles = makeStyles(styles);

export default function SectionCompletedExamples({categoryList, category, setCategory}) {
  const classes = useStyles();
  const [categoryValue, setCategoryValue] = React.useState(['']);
  const [checked, setChecked] = React.useState(true);

/*   React.useEffect(() => {
    setCategory(categoryValue);
    } , [categoryValue]);
 */
  const handleChange = (event) => {
    setCategory(event.target.value);
  };

  const handleChangeCheckbox = (event) => {
    setChecked(event.target.checked);
  };
  
  return (
    <div className={classes.section}>
      <div className={classes.container}>

          <GridContainer justify="space-between" alignItems="center">
            <GridItem xs={8} sm={8} md={6} lg={6}>
              <h3>Filtre</h3>
            </GridItem>

            <GridItem xs={4} sm={4} md={2} lg={2}>
              <FormControl  className={classes.formControl}>
              <InputLabel id="pays">Pays</InputLabel>
              <Select
                labelId="pays"
                id="simple-select-pays"
                label="Pays">
                <MenuItem default value="france">
                  <FlagRoundedIcon color='primary'/><em>France</em>
                </MenuItem>
              </Select>
            </FormControl>
            </GridItem>
          </GridContainer>

          <GridContainer>
            <GridItem xs={12} sm={12} md={6} lg={6}>
              <FormControl variant="outlined"  className={classes.formControl}>
                <InputLabel id="demo-simple-select-outlined-label">Categorie de véhicules</InputLabel>
                <Select
                  labelId="demo-simple-select-outlined-label"
                  id="demo-simple-select-outlined"
                  value={category}
                  onChange={handleChange}
                  label="Categorie de véhicules"
                  >
                  <MenuItem value="">
                    <em>toute</em>
                  </MenuItem>
                  {categoryList.map((category, key)=><MenuItem  key={key}  value={category.value}>{category.name}</MenuItem>)}
                </Select>
              </FormControl>
            </GridItem>

            <GridItem xs={12} sm={12} md={6} lg={6}>
          <GridContainer   justify="space-around">
              <FormControlLabel
                control={<Checkbox
                color="primary" checked={checked} onChange={handleChangeCheckbox} name="checkedA" />}
                label="Promo "
              />
              <FormControlLabel
                control={<Checkbox checked={checked} onChange={handleChangeCheckbox} name="checkedA" />}
                label="Premium"
              />
          </GridContainer>
            </GridItem>
          </GridContainer>
      </div>
    </div>
  );
}
