import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
// core components
import Card from "components/Card/Card.js";
/* 
import styles from "assets/jss/material-kit-react/components/customTabsStyle.js"; */
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from "components/CustomButtons/Button.js";
import Slide from "@material-ui/core/Slide";
import { Link } from "react-router-dom";
import Divider from '@material-ui/core/Divider';
import DoneAllRoundedIcon from '@material-ui/icons/DoneAllRounded';
import AcUnitRoundedIcon from '@material-ui/icons/AcUnitRounded';
import EuroSymbolRoundedIcon from '@material-ui/icons/EuroSymbolRounded';
import CardGiftcardIcon from '@material-ui/icons/CardGiftcard';
import NewReleasesRoundedIcon from '@material-ui/icons/NewReleasesRounded';

import Skeleton from '@material-ui/lab/Skeleton';

import styles from "assets/jss/material-kit-react/views/catalogue/ShowCarsStyles.jsx";
import s from "assets/jss/material-kit-react/views/componentsSections/javascriptStyles.js";

const useStyle = makeStyles(styles);
const u = makeStyles(s);

export default function(props) {
  const classe = useStyle();
  const {cars, category, loading} = props;

  return (
    <div className={classe.section}>
      <div className={classe.container}>
        <Show cars={cars} category={category} loading={loading}/>
      </div>
    </div>);
}

const carStyles = makeStyles((theme) => ({
  root: {
  },
  details: {
    display: 'flex',
    flexDirection: 'column',
  },
  content: {
  },
  cover: {
    minHeight: 160,
    width: '100%',
  },
  b:{
    fontWeight: 'bold',
    color: 'black',
  }
}));
function Show({category, cars, ...props}) {
  const classes = carStyles();

  function ShowCar({car}) {
    return (<Card className={classes.root}>
      <GridContainer direction="row" justify="space-around" alignItems="center">
        <GridItem xs={12} sm={12} md={4} lg={4}>
      <CardMedia
        className={classes.cover}
        image={car.image}
        title={car.name}
      />
      </GridItem>
        <GridItem xs={12} sm={12} md={6} lg={6}>
      <div className={classes.details}>
        <CardContent className={classes.content}>
          
      <GridContainer direction="row" justify="flex-end">
        <GridItem xs={12} sm={12} md={6} lg={6}>
          <Typography component="h5" variant="h5" color='primary'>
            {car.name}
          </Typography>
          {car.description ?<Typography>
                {car.description}
                </Typography> : null}
          <Typography variant="subtitle1" color="secondary">
          <div>
            {car.price} <sup>$</sup>/jour</div>
          </Typography>
      </GridItem>
        <GridItem xs={12} sm={12} md={6} lg={6}>
          {car.climatisation ?<Typography  color="textSecondary">
          <AcUnitRoundedIcon color='primary'/> Climatisation
          </Typography> : null}
          {car.boite ? <Typography color="textSecondary">
            <DoneAllRoundedIcon color='primary'/> Boite automatique
          </Typography> : null}
          
      <Divider />
      <GridContainer direction="row" justify="flex-end">
        <GridItem xs={6} sm={6} md={6} lg={6}>
          <Typography color="textSecondary">
            {car.porte} Portes 
          </Typography>
          <Typography color="textSecondary">
            {car.place} Places
          </Typography>
      </GridItem>
        <GridItem xs={6} sm={6} md={6} lg={6}>
          <Typography color="textSecondary">
            {car.sac} Sacs
          </Typography>
          <Typography color="textSecondary">
            {car.valise} valises 
          </Typography>
      </GridItem>
      </GridContainer>
      </GridItem>
      </GridContainer>
        </CardContent>
        
      </div>
      </GridItem>
      <GridItem  xs={12} sm={12} md={12} lg={12}>
      <Divider />
      <Grid 
  container
  direction="row"
  justify="space-around"
  alignItems="center"
      >
      <div>
      {car.promo ? <Button color="danger" simple className={""}><CardGiftcardIcon/> Promo</Button>  : null}
      {car.premium ? <Button color="primary" simple className={''}><NewReleasesRoundedIcon/> premium</Button> : null }
      </div>
          <Link to={`/commande/${car.id}`}> 
            <Button color="rose" className={classes.b}>reserver</Button>
          </Link>
        </Grid>
      </GridItem>
      </GridContainer>
    </Card>)
  }
  const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="down" ref={ref} {...props} />;
  });
  
  Transition.displayName = "Transition";
  
  const flilterCars = cars.filter(flilterCar => flilterCar.category.includes(category))
    return(<div>
      {
        props.loading 
          ?(<><Typography style={{textAlign: 'center', paddingTop: 10}}>Chargement...</Typography>
          <GridContainer>
            <Skeleton animation="wave" variant="rect" width={`${100}%`} height={`${30}vh`}  />
        </GridContainer></>)
          :(<><Typography style={{textAlign: 'center', paddingTop: 10}}>{'  '}{flilterCars.length}{category !=="" ? ' véhicules trouvés pour la catégorie '+category  : ' véhicules'}</Typography>
          <GridContainer>
            {flilterCars.map( (car, key)=> <GridItem key={key}  xs={12} sm={12} md={12}> <ShowCar car={car}/> </GridItem>)}
          </GridContainer>
          </>)
        
      }
      </div>)   
}