import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// @material-ui/icons
// core components
import Footer from "components/Footer/Footer.js";
// sections for this page
import FilterSection from "./Sections/FilterSection.jsx";
import ShowCarsSection from "./Sections/ShowCarsSection.jsx";

import axiosInstance from 'axios.js';

import styles from "assets/jss/material-kit-react/views/Catalogue.jsx";

const useStyles = makeStyles(styles);

export default function Home(props) {
  const classes = useStyles();
  const { ...rest } = props;
  const [cars, setCars] = React.useState([]);
  const [category, setCategory] = React.useState("");
  const [loading, setLoading] = React.useState(true);
  const categoryList = [
    {
      name: 'Monospace',
      value: 'monospace'
    },{
      name: 'Berline',
      value: 'berline'
    },{
      name: 'Cabriolet',
      value: 'cabriolet'
    },{
      name: 'SUV',
      value: 'suv'
    },{
      name: 'Coupé',
      value: 'coupe'
    },
  ]

  const {baseURL} = props;
  React.useEffect(() => {
		axiosInstance.get(`${baseURL}client-api/car-list/`).then((res) => {
			const allPosts = res.data;
      setCars(allPosts);
      setLoading(false);
		}).catch((err) => setLoading(false));
	}, []);

  return (
    <div>
      <div className={classNames(classes.main, classes.mainRaised)}>
          <div className={classes.title}>
             <h2>Liste de véhicule disponible</h2>
          </div>
        <FilterSection categoryList={categoryList} category={category} setCategory={setCategory}/>
        <ShowCarsSection cars={cars} category={category} loading={loading} />
      </div>
      <Footer />
    </div>
  );
}
